import React, { Component } from 'react';
import classes from './NewCountryForm.module.css';

class NewCountryForm extends Component {

    state = {
        countryName: '',
        covidPositive: false,
        idvIndex: ''
    }

    onSubmit = e => {
        e.preventDefault();
        console.log(this.state);
    }


    render() {
        return (
            <div>
                <form onSubmit={this.onSubmit}>
                    <div className="form-group">
                        <label htmlFor="countryName">Country name</label>
                        <input
                            type="text"
                            className="form-control"
                            id="countryName"
                            placeholder="Enter country name"
                            value={this.state.countryName}
                            onChange={e => this.setState({countryName: e.target.value})} />
                    </div>
                    
                    <div className="form-group">
                        <label htmlFor="idvIndex">Country IDV</label>
                        <input
                            type="number"
                            className="form-control"
                            id="idvIndex"
                            placeholder="Enter country IDV index"
                            value={this.state.idvIndex}
                            onChange={e => this.setState({idvIndex: e.target.value})} />
                    </div>

                    <div className="form-check">
                        <input
                            type="checkbox"
                            className="form-check-input"
                            id="covidPositive"
                            value={this.state.covidPositive}
                            onChange={e => this.setState({covidPositive: e.target.checked}) }/>
                        <label className="form-check-label" htmlFor="covidPositive">Country is COVID-19 positive</label>
                    </div>

                    <br />

                    <button type="submit" className="btn btn-primary">Submit</button>
                </form>
            </div>
        );
    }
}

export default NewCountryForm;