import React, { Component } from 'react';
import Input from '../../../components/UI/Input/Input';
import classes from './NewPatientForm.module.css';
import axios from '../../../axios';

class NewPatientForm extends Component {

    state = {
        form: {
            firstName: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Firstname'
                },
                validation: {},
                value: '',
                valid: true,
                touched: false,
                label: 'Firstname: ',
                show: false,
                loading: false
            },
            lastName: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Lastname'
                },
                validation: {},
                value: '',
                valid: true,
                touched: false,
                label: 'Lastname: ',
                show: false,
                loading: false
            },
            country: {
                elementType: 'select',
                elementConfig: {
                    options: []
                },
                validation: {},
                value: '',
                valid: true,
                touched: false,
                label: 'Country:',
                show: false,
                loading: false
            },
            covidPositiveContact: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Had contact with COVID-19: ',
                show: false,
                loading: false
            },
            cold: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has cold: ',
                show: false,
                loading: false
            },
            lastFever: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'Last fever'
                },
                validation: {},
                value: "",
                valid: true,
                touched: false,
                label: 'Last fever: ',
                show: false,
                loading: false
            },
            respiratoryRate: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'Respiratory rate'
                },
                validation: {},
                value: "",
                valid: true,
                touched: false,
                label: 'Respiratory rate: ',
                show: false,
                loading: false
            },
            hypoxia: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has hypoxia: ',
                show: false,
                loading: false
            },
            soreThroat: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has sore throat: ',
                show: false,
                loading: false
            },
            cough: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has cough: ',
                show: false,
                loading: false
            },
            dyspnea: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has dyspnea: ',
                show: false,
                loading: false
            },
            tachypnea: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has tachypnea: ',
                show: false,
                loading: false
            },
            alc: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'ALC'
                },
                validation: {},
                value: "",
                valid: true,
                touched: false,
                label: 'Absolute lymphocite count: ',
                show: false,
                loading: false
            },
            pneumonia: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has pneumonia: ',
                show: false,
                loading: false
            },
            oxygenSaturation: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'Oxygen saturation'
                },
                validation: {},
                value: "",
                valid: true,
                touched: false,
                label: 'Oxygen saturation: ',
                show: false,
                loading: false
            },
            nonHospitalPneumonia: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has non hospital pneumonia: ',
                show: false,
                loading: false
            },
        },
    }

    componentDidMount() {
        axios.get("country/get/all")
            .then(res => {
                const countryOptions = res.data.map(country => {
                    return {
                        value: country.countryName,
                        displayValue: country.countryName
                    };
                })
                const updatedForm = { ...this.state.form };
                const updatedElement = { ...updatedForm.country };
                const updatedElementConfig = { ...updatedElement.elementConfig };
                updatedElementConfig.options = [...countryOptions];
                updatedElement.elementConfig = updatedElementConfig;
                updatedForm.country = updatedElement;
                this.setState({ form: updatedForm });
            })
            .catch(err => console.log(err));
    }

    inputChangedHandler = (event, inputIdentifier) => {
        const updatedForm = { ...this.state.form };

        const updatedFormElement = { ...updatedForm[inputIdentifier] };

        console.log(updatedFormElement);
        if (updatedFormElement.elementConfig.type === "checkbox") {
            updatedFormElement.value = event.target.checked;
            console.log(updatedFormElement.value);
        } else {
            updatedFormElement.value = event.target.value;
        }
        // updatedFormElement.valid = checkFormValidity(updatedFormElement.value, updatedFormElement.validation);
        updatedFormElement.touched = true;

        updatedForm[inputIdentifier] = updatedFormElement;

        // let formIsValid = true;
        // for (let input in updatedForm) {
        //     formIsValid = updatedForm[input].valid && formIsValid;
        // }

        this.setState({ form: updatedForm });
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        const formData = {};

        for (let formElementIdentifier in this.state.form) {
            formData[formElementIdentifier] = this.state.form[formElementIdentifier].value;
        }

        console.log(formData);
    }


    render() {

        const formElementsArray = [];

        for (let key in this.state.form) {
            formElementsArray.push({
                id: key,
                config: this.state.form[key]
            });
        }

        let form = (
            formElementsArray.map(formElement => {
                return <Input
                    key={formElement.id}
                    elementType={formElement.config.elementType}
                    elementConfig={formElement.config.elementConfig}
                    value={formElement.config.value}
                    changed={(event) => this.inputChangedHandler(event, formElement.id)}
                    invalid={!formElement.config.valid}
                    shouldValidate={formElement.config.validation}
                    touched={formElement.config.touched}
                    label={formElement.config.label}
                    id={formElement.id}
                />
            })
        );


        return (
            <div className="container">
                <form onSubmit={this.onFormSubmit}>
                    {form}
                    <div className="row">
                        <button className="btn btn-primary" type="submit">Submit</button>
                    </div>
                </form>

            </div>
        );
    }
}

export default NewPatientForm;